import { Pegawai_payroll } from 'src/app/sdk/payroll/models/pegawai-_payroll';
import { Component, OnInit, ViewChild, AfterViewInit, OnDestroy } from '@angular/core';
import { TahapService } from '../tahap.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { PayrollPegawaiThrService, PayrollPegawaiBonusService } from 'src/app/sdk/payroll/services';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NzNotificationService, NzTableComponent } from 'ng-zorro-antd';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';


export interface VirtualDataInterface {
    index: number;
    name: string;
    age: number;
    address: string;
}

@Component({
    selector: 'app-tahap-review',
    templateUrl: './tahap-review.component.html',
    styleUrls: ['./tahap-review.component.scss']
})
export class TahapReviewComponent implements OnInit {



    constructor(
        private tahapService: TahapService,
        private spinner: NgxSpinnerService,
        private payrollPegawaiBonusService: PayrollPegawaiBonusService,
        private pesan: PesanService,
        private notification: NzNotificationService
    ) { }




    periode = 0;
    ngOnInit(): void {
        this.periode = this.tahapService.periode;
        this.getRekap()

        this.tahapService.next.next(false)
    }


    listData: any[] = []
    rekap: any[] = []
    cek_status: boolean = true;
    getRekap() {
        this.cek_status = true;
        this.spinner.show()
        let param: any = {
            periode: this.periode,
        }
        this.payrollPegawaiBonusService.getPayrollPegawaiBonuses(JSON.stringify(param)).subscribe(
            data => {

                let datas: any = data.data;
                this.listData = datas.list;
                this.rekap = datas.rekap;

                this.spinner.hide();

                this.listData.forEach(item => {
                    if (item.bersih == null) {
                        this.cek_status = false;
                    }

                })

                console.log(this.cek_status)
                if (!this.cek_status) {
                    this.tahapService.next.next(false)
                }
                else {
                    this.tahapService.next.next(true)
                }
            }
        )
    }

    listProsess: any = [];
    prosessRekap() {
        this.listProsess = [];
        this.spinner.show()
        this.listData.forEach(element => {
            this.prosessRekapOneByOne(element.id_pegawai_bonus, true);
        });
    }

    prosessRekapOneByOne(id_pegawai_bonus: string, action = false) {
        let params: any = {
            periode: this.periode,
            id_pegawai_bonus: id_pegawai_bonus,
        }
        this.spinner.show()
        this.payrollPegawaiBonusService.putPayrollPegawaiBonusesId({ id: id_pegawai_bonus, body: params }).subscribe(
            data => {
                if (action == true) {
                    this.listProsess.push(1);
                    console.log(this.listProsess.length)
                    console.log('xx' + this.listData)
                    if (this.listProsess.length == this.listData.length) {
                        this.spinner.hide();
                        this.notification.create(
                            'success',
                            'Berhasil di proses',
                            'semua komponen berhasil di prosess'
                        );
                        this.getRekap()
                    }
                }
                else {
                    this.spinner.hide();
                    this.getRekap()
                }
            },
            error => {
                // this.spinner.hide();
                // alert('error pada ' + error)
                this.notification.create(
                    'warning',
                    'Gagal di proses',
                    error.message
                );
                // this.getRekap()

                this.listProsess.push(1);
                if (this.listProsess.length == this.listData.length) {
                    this.spinner.hide();
                    this.getRekap()
                }
            }
        )
    }

    viewDetail(id_pegawai_bonus) {
        this.visible = true;
    }

    visible = false;


    close(): void {
        this.visible = false;
    }

}
