import { Component, OnInit } from '@angular/core';
import { TahapService } from '../tahap.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { PayrollPegawaiBonusService, PayrollPayrollRekapService } from 'src/app/sdk/payroll/services';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NzNotificationService } from 'ng-zorro-antd';

@Component({
    selector: 'app-tahap-confirm',
    templateUrl: './tahap-confirm.component.html',
    styleUrls: ['./tahap-confirm.component.scss']
})
export class TahapConfirmComponent implements OnInit {

    constructor(
        private tahapService: TahapService,
        private spinner: NgxSpinnerService,
        private payrollPayrollRekapService: PayrollPayrollRekapService,
        private pesan: PesanService,
        private notification: NzNotificationService
    ) { }

    periode = 0;
    pembetulan = ''
    is_confirm: boolean = false;
    ngOnInit() {
        this.periode = this.tahapService.periode;
        this.pembetulan = this.tahapService.pembetulan;
        this.tahapService.next.next(false)
    }

    confirm() {
        let param: any = {
            periode: this.periode,
            pembetulan: this.pembetulan,
            confirm: true
        }
        this.payrollPayrollRekapService.putPayrollPayrollRekapsId({ id: 'confirm', body: param }).subscribe(
            data => {
                this.spinner.hide();
                this.is_confirm = true;
                this.tahapService.next.next(true)
            }
        )
    }

}
