import { Component, OnInit } from '@angular/core';
import { TahapService } from '../tahap.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { PayrollTemplateKomponenService } from 'src/app/sdk/payroll/services';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NzNotificationService } from 'ng-zorro-antd';

@Component({
    selector: 'app-tahap-komponen',
    templateUrl: './tahap-komponen.component.html',
    styleUrls: ['./tahap-komponen.component.scss']
})
export class TahapKomponenComponent implements OnInit {

    constructor(
        private tahapService: TahapService,
        private spinner: NgxSpinnerService,
        private payrollTemplateKomponenService: PayrollTemplateKomponenService,
        private pesan: PesanService,
        private notification: NzNotificationService
    ) { }

    periode = 0;
    ngOnInit(): void {
        this.periode = this.tahapService.periode;
        this.getRekap()
        this.tahapService.next.next(false)

    }


    listData: any[] = []
    listDataKomponen: any[] = []

    cek_status: boolean = true;
    getRekap(reload = false) {
        this.cek_status = true;
        let param: any = {
            periode: this.periode,
            for_module: 'bonus',
            reload: reload
        }
        this.payrollTemplateKomponenService.postPayrollTemplateKomponens(param).subscribe(
            data => {

                let datas: any[] = data.data['pegawai'];
                this.listDataKomponen = data.data['komponen'];
                let komponens: any[] = data.data['komponen'];
                datas.forEach((item, index) => {
                    let id_pegawai = item.id_pegawai;
                    let cek = komponens.filter((kom) => {
                        if (kom.id_pegawai == id_pegawai) {
                            return true;
                        }
                        if (kom.nilai_akhir == null && kom.id_template != null) {
                            this.cek_status = false
                        }
                    })
                    datas[index]['item'] = cek;


                })
                this.listData = datas;
                this.spinner.hide()

                console.log(this.cek_status)
                if (!this.cek_status) {
                    this.tahapService.next.next(false)
                }
                else {
                    this.tahapService.next.next(true)
                }
            }
        )
    }

    listProsess: any = [];
    prosessRekap() {
        this.listProsess = [];
        this.spinner.show()
        this.listDataKomponen.forEach(element => {
            if (element.id_template) {
                this.prosessRekapOneByOne(element.id_template, element.id_pegawai, true);
            }
            else {
                this.listProsess.push(1);
            }

        });
    }

    prosessRekapOneByOne(id_template: string, id_pegawai, action = false) {
        let params: any = {
            periode: this.periode,
            for_module: 'bonus',
            id_pegawai: id_pegawai,
        }
        this.payrollTemplateKomponenService.putPayrollTemplateKomponensId({ id: id_template, body: params }).subscribe(
            data => {
                if (action == true) {
                    this.listProsess.push(1);
                    console.log(this.listProsess.length)
                    console.log('xx' + this.listDataKomponen)
                    if (this.listProsess.length == this.listDataKomponen.length) {
                        this.spinner.hide();
                        this.notification.create(
                            'success',
                            'Berhasil di proses',
                            'semua komponen berhasil di prosess'
                        );
                        this.getRekap()
                    }
                }
                else {
                    this.spinner.hide();
                    this.getRekap()
                }
            },
            error => {
                // this.spinner.hide();
                // alert('error pada ' + error)
                this.notification.create(
                    'warning',
                    'Gagal di proses',
                    error.message
                );
                // this.getRekap()

                this.listProsess.push(1);
                if (this.listProsess.length == this.listDataKomponen.length) {
                    this.spinner.hide();
                    this.getRekap()
                }
            }
        )
    }

}
