import { Component, OnInit } from '@angular/core';
import { TahapService } from '../tahap.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { PayrollPayrollRekapService } from 'src/app/sdk/payroll/services';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NzNotificationService } from 'ng-zorro-antd';
import { CompanyKantorService } from 'src/app/sdk/hr/services';
import { Kantor } from 'src/app/sdk/hr/models';
import { Komponen_perusahaan, Payroll_rekap } from 'src/app/sdk/payroll/models';

@Component({
    selector: 'app-tahap-template',
    templateUrl: './tahap-template.component.html',
    styleUrls: ['./tahap-template.component.scss']
})
export class TahapTemplateComponent implements OnInit {

    constructor(
        private tahapService: TahapService,
        private spinner: NgxSpinnerService,
        private payrollPayrollRekapService: PayrollPayrollRekapService,
        private pesan: PesanService,
        private kantorService: CompanyKantorService,
        private notification: NzNotificationService
    ) { }

    periode: number;
    pembetulan: string = '';
    ngOnInit(): void {
        this.periode = this.tahapService.periode;
        this.pembetulan = this.tahapService.pembetulan;

        this.getRekap()

    }





    listData: Payroll_rekap[] = []

    getRekap(reload = false) {
        this.tahapService.next.next(true)

        this.is_edit = []
        this.listData = []
        let param: any = {
            periode: this.periode,
            pembetulan: this.pembetulan,
            reload: reload
        }
        this.payrollPayrollRekapService.postPayrollPayrollRekaps(param).subscribe(
            data => {
                let datas: any = data.data;

                datas.forEach((item, index) => {
                    let l = item;
                    l.nomor_bukti_potong = "1.1-" + (item['masa_pajak']) + "." + ((this.periode.toString()).substring(2, 4)) + "-" + (index + 1).toString().padStart(7, "0");
                    this.listData.push(l);

                    this.is_edit.push(
                        {
                            id_payroll_rekap: item.id_payroll_rekap,
                            is_edit: false
                        }
                    )

                    if (item.netto < 1 || this.is_on_update) {
                        console.log(item.netto)
                        this.is_on_update = true
                        this.tahapService.next.next(false)
                    }
                });

                this.spinner.hide()


            }
        )
    }

    is_edit = [];
    togleUpdate(id_payroll_rekap) {
        this.is_edit.forEach((item, index) => {
            if (item.id_payroll_rekap == id_payroll_rekap) {
                this.is_edit[index].is_edit = true;
            }
            else {
                this.is_edit[index].is_edit = false;
            }
        })
    }
    cek_edit(index) {
        return this.is_edit[index].is_edit;
    }

    is_on_update = false
    updateDate(item: Payroll_rekap) {
        this.spinner.show()
        this.payrollPayrollRekapService.putPayrollPayrollRekapsId({ id: 'update', body: item }).subscribe(
            data => {
                this.spinner.hide()
                this.is_on_update = true
                this.getRekap()
            }
        )
    }

    listProsess = []
    prosessRekap() {
        this.listProsess = [];
        this.is_edit.forEach(element => {
            if (element.id_payroll_rekap) {
                this.prosessRekapOneByOne(element.id_payroll_rekap);
            }
            else {
                this.listProsess.push(1);
            }

        });
    }

    prosessRekapOneByOne(id_payroll_rekap, action = true) {

        this.spinner.show()
        let params: any = {
            id_payroll_rekap: id_payroll_rekap,
        }
        this.payrollPayrollRekapService.putPayrollPayrollRekapsId({ id: "proses", body: params }).subscribe(
            data => {
                if (action == true) {
                    this.listProsess.push(1);
                    console.log(this.listProsess.length)
                    if (this.listProsess.length == this.is_edit.length) {
                        this.spinner.hide();
                        this.notification.create(
                            'success',
                            'Berhasil di proses',
                            'semua komponen berhasil di prosess'
                        );
                        this.getRekap()
                    }
                }
                else {
                    this.spinner.hide();
                    this.getRekap()
                }
            },
            error => {
                // this.spinner.hide();
                // alert('error pada ' + error)
                this.notification.create(
                    'warning',
                    'Gagal di proses',
                    error.message
                );
                // this.getRekap()

                this.listProsess.push(1);
                if (this.listProsess.length == this.is_edit.length) {
                    this.spinner.hide();
                    this.getRekap()
                }
            }
        )
    }

}
