import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { TahapService } from '../tahap.service';
import { PayrollPegawaiBonusService, PayrollPayrollRekapService } from 'src/app/sdk/payroll/services';
import { NgxSpinnerService } from 'ngx-spinner';


@Component({
    selector: 'app-tahap',
    templateUrl: './tahap.component.html',
    styleUrls: ['./tahap.component.scss']
})
export class TahapComponent implements OnInit {

    constructor(
        private tahapService: TahapService,
        private payrollPayrollRekapService: PayrollPayrollRekapService,
        private router: Router,
        private spinner: NgxSpinnerService,
        private pesan: PesanService
    ) { }

    periode: Date = new Date();
    pembetulan: string = '0';
    ngOnInit() {
        this.tahapService.periode = this.periode.getFullYear();
        this.tahapService.pembetulan = this.pembetulan;

        this.tahapService.getNext().subscribe(
            data => {
                this.if_next = data;
            }
        )
    }


    ngModelChange() {
        this.tahapService.periode = this.periode.getFullYear();
        this.tahapService.pembetulan = (this.pembetulan);

        this.cekApakahPeriodeAvaible()
    }

    cekApakahPeriodeAvaible() {
        this.spinner.show()
        let param = {
            periode: this.periode.getFullYear(),
            cek_data: this.periode,
            pembetulan: this.pembetulan
        }
        this.payrollPayrollRekapService.getPayrollPayrollRekaps(JSON.stringify(param)).subscribe(
            data => {
                this.spinner.hide()
                if (data.data['total'] > 0) {
                    this.pesan.pesanWarningForm('Periode ini sudah digunakan, gunakan tanggal Lain');
                    this.if_next = false;
                }
                else {
                    this.if_next = true;
                    console.log(this.if_next)
                }
            },
            err => {
                this.spinner.hide()

            }
        )
    }


    index = 'First-content';
    current = 0;

    pre(): void {
        this.if_next = true
        this.current -= 1;
        this.changeContent();
    }

    if_next: boolean = true;
    next(): void {

        if (this.current == 0) {
            this.spinner.show()
            let param = {
                periode: this.periode.getFullYear(),
                cek_data: this.periode,
                pembetulan: this.pembetulan
            }
            this.payrollPayrollRekapService.getPayrollPayrollRekaps(JSON.stringify(param)).subscribe(
                data => {
                    console.log(data.data)
                    this.spinner.hide()
                    if (data.data['total'] > 0) {
                        this.pesan.pesanWarningForm('Periode ini sudah digunakan, gunakan tanggal Lain');
                        this.if_next = false;
                        return false;
                    }
                    else {
                        this.if_next = true;
                        this.current += 1;
                        this.changeContent();
                    }

                },
                err => {
                    this.spinner.hide()
                }
            )
        }
        else {
            if (this.if_next) {
                this.current += 1;
                this.changeContent();
            }
            else {
                this.pesan.pesanWarningForm('mohon selesaikan tahap ini!')
            }

        }

    }

    done(): void {
        if (this.if_next) {
            this.current += 1;
            this.router.navigate(['/hr/payroll/module/laporan/tahunan/list']);
        }
        else {
            this.pesan.pesanWarningForm('mohon selesaikan tahap ini!')
        }

    }

    changeContent(): void {
        switch (this.current) {
            case 0: {
                this.index = 'First-content';
                break;
            }
            case 1: {
                this.index = 'Second-content';
                break;
            }
            case 2: {
                this.index = 'third-content';
                break;
            }
            default: {
                this.index = 'error';
            }
        }
    }


}
